.profile__about {
    margin: 0;
    color: #fff;
    align-self: flex-end;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 950px) {
    .profile__about {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
    }
}