.popup {
    visibility: hidden;
    opacity:0;
    transition: visibility 0.2s, opacity 0.2s linear;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: fixed;
    background-color: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
}