.popup__field {
    width: 358px;
    min-height: 44px;
    margin: auto;
    position: relative;
    display: flex;
    flex-direction: column;

}

.popup__field:last-of-type {
    margin-bottom: 10px;
}

@media screen and (max-width: 900px) {
    .popup__field {
        max-width: 238px;
    }
}
